import uniqBy from 'lodash/uniqBy';

const API_URL = 'http://bibliography-api.testgc.ru/site/';

export const booksInitialState = {
    books: {
    	literature: { items: [], page: 0, selectedItemId: -1 },
    	myLiterature: { items: [], buildLink: 'http://bigraph.ru/c/Jgeo_F7FG' }
    }
};
  
export const booksActions = {
    getBooks: async(state, { dispatch, addNew }) => {
        const searchInputs = state.inputs.search;
        const literature = state.books.literature;

        const books = addNew ? literature.items : [];
        const page = addNew ? literature.page + 1 : literature.page;
        const selectedItemId = addNew ? literature.selectedItemId : -1;
        
        if (searchInputs.valid) {
            const { name, publishedDate, publisher, category, language } = searchInputs;
            const searchInputsArr = [name, publishedDate, publisher, category, language];
            const limit = 15;

            if (searchInputsArr.every(input => input.value.trim() === '') === false) {
                const response = await fetch(`${API_URL}get-books?query=${name.value.trim()}&publishedDate=${publishedDate.value.trim()}&publisher=${publisher.value.trim()}&category=${category.value.trim()}&language=${language.value.trim()}&limit=${limit}&page=${page}`);
                const data = await response.json();
                
                if (data && data.status === 'success') {
                    const items = addNew ? [ ...state.books.literature.items, ...data.data ] : data.data;
                    books.push(...items);
                }
            }
        }

        dispatch({ type: 'setBooks', payload: { items: uniqBy(books, 'id'), name: 'literature', page: page, selectedItemId: selectedItemId }});
    },

    setBooks: (state, { items, name, page, selectedItemId }) => {
        return {
            books: {
                ...state.books,
                [name]: { 
                    ...state.books[name], 
                    items: items, 
                    page: page,
                    selectedItemId: selectedItemId
                }
            }
        }
    },

    clearBooks: (state, { name }) => {
        return {
            books: {
                ...state.books,
                [name]: { ...state.books[name], items: [] }
            }
        }
    },

    selectListItem: (state, { dispatch, name, keyCode }) => {
        const booksGroup = state.books[name];
        const booksItems = Object.assign([], booksGroup.items);

        let selectedItemId = booksGroup.selectedItemId > -1 ? booksGroup.selectedItemId : -1;
    
        if (keyCode === 'ArrowDown') {
            if (booksItems[selectedItemId] && selectedItemId !== booksItems.length - 1) booksItems[selectedItemId].active = false;

            if (booksItems[selectedItemId + 1]) {
                booksItems[selectedItemId + 1].active = true;
                selectedItemId++;
            }
        }

        if (keyCode === 'ArrowUp') {
            if (booksItems[selectedItemId] && selectedItemId !== 0) booksItems[selectedItemId].active = false;

            if (booksItems[selectedItemId - 1]) {
                booksItems[selectedItemId - 1].active = true;
                selectedItemId--;
            }
        }

        if (keyCode === 'ArrowRight') {
            dispatch({ type: 'addToMyLiterature', payload: { book: booksItems[selectedItemId] } });
        } else {
            return {
                books: {
                    ...state.books,
                    [name]: {
                        ...state.books[name],
                        items: booksItems,
                        selectedItemId: selectedItemId
                    }
                }
            }
        }
    },

    addToMyLiterature: (state, { book }) => {
        book = typeof book === 'string' ? JSON.parse(book): book;
        const books = [ ...state.books.myLiterature.items, book];

        return {
            books: {
                ...state.books,
                myLiterature: {
                    ...state.books.myLiterature,
                    items: uniqBy(books, 'id')
                }
            }
        }
    },

    removeFromMyLiterature: (state, { bookIndex }) => {
        const books = state.books.myLiterature.items;

        return {
            books: {
                ...state.books,
                myLiterature: {
                    ...state.books.myLiterature,
                    items: [ ...books.slice(0, +bookIndex), ...books.slice(+bookIndex + 1, books.length) ]
                }
            }
        }
    }
};