import React from 'react';

import { ReactComponent as Logo } from 'static/icons/logo.svg';
import { ReactComponent as GoldCarrotLogo } from 'static/icons/gold-carrot-logo.svg';
import { ReactComponent as CopyIcon } from 'static/icons/copy.svg';
import { ReactComponent as CrossTinyIcon } from 'static/icons/cross-tiny.svg';
import { ReactComponent as CrossIcon } from 'static/icons/cross.svg';
import { ReactComponent as LinkIcon } from 'static/icons/link.svg';
import { ReactComponent as PlusIcon } from 'static/icons/plus.svg';
import { ReactComponent as SearchIcon } from 'static/icons/search.svg';

const Icon = ({ iconName, className }) => {
    switch(iconName) {
        case 'logo':
            return <Logo className = { className } />;
        case 'gold-carrot-logo':
            return <GoldCarrotLogo className = { className } />
        case 'copy':
            return <CopyIcon className = { className } />;
        case 'cross':
            return <CrossIcon className = { className } />;
        case 'cross-tiny':
            return <CrossTinyIcon className = { className } />;
        case 'link':
            return <LinkIcon className = { className } />;
        case 'plus':
            return <PlusIcon className = { className } />;
        case 'search':
            return <SearchIcon className = { className } />;
        default:
            break;
    }
};

export { Icon };