import React, { Fragment } from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter as Router } from 'react-router-dom';

import { StoreProvider } from 'store/useStore';

import Header from 'components/dumb-components/Header/Header';

import Main from 'components/smart-components/Main/Main';

import './index.scss';

const Root = () => (
	<StoreProvider>
		<Router>
			<Fragment>
				<Header />
				<Main />
			</Fragment>
		</Router>
	</StoreProvider>
);

ReactDOM.render(<Root />, document.getElementById('root'));

