import React, { useCallback } from 'react';

import MyLiteratureEmpty from './MyLiteratureEmpty';
import MyLiteratureBuild from './MyLiteratureBuild';

import { Text, ButtonsContainer, Button, Icon, List, ListTitle, ListItems, ListItem } from 'components/dumb-components';

import { useStore } from 'store/useStore';

import './MyLiterature.scss';

const MyLiterature = ({ writeToClipBoard, openBookLink, clearBooks }) => {
    const { state, dispatch } = useStore();

    const books = state.books.myLiterature.items;
    const buildLink = state.books.myLiterature.buildLink;

    const removeFromMyLiterature = useCallback(event => {
        dispatch({ type: 'removeFromMyLiterature', payload: { bookIndex: event.currentTarget.dataset.bookIndex } });
    }, [dispatch]);


    return (
        <div className = 'my-literature'>
            { books.length !== 0 ? (
                <List className = 'on-dark'>
                    <ListTitle>
                        <Text className = 'list__title-text text'>Моя литература</Text>
                        <ButtonsContainer className = 'list__title-buttons-container'>
                            <Button className = 'list__title-btn advanced-btn on-dark' data-books-name = 'myLiterature' onClick = { clearBooks }>
                                <Text className = 'list__title-btn-text advanced-btn__text btn__text'>очистить</Text>
                                <Icon className = 'list__title-btn-icon advanced-btn__icon btn__icon' iconName = 'cross-tiny' />
                            </Button>
                        </ButtonsContainer>
                    </ListTitle>

                    <MyLiteratureBuild buildLink = { buildLink } writeToClipBoard = { writeToClipBoard } />

                    <ListItems>
                        { books.map(book => (
                            <ListItem key = { book.id }>
                                <Text className = 'list__items-item-text text'>{ `${book.title}` }</Text>
                                <ButtonsContainer className = 'list__items-item-buttons-container'>
                                    <Button className = 'list__items-item-btn icon-btn on-dark' data-book-link = { book.link }
                                            title = 'Перейти на сайт с книгой' onClick = { openBookLink }>
                                        <Icon className = 'list__items-item-btn-icon btn__icon' iconName = 'link' />
                                    </Button>
                                    <Button className = 'list__items-item-btn icon-btn on-dark' data-clipboard-text = { book.desc || 'У данной книги нету описания' }
                                            title = 'Скопировать описание в буфер обмена' onClick = { writeToClipBoard }>
                                        <Icon className = 'list__items-item-btn-icon btn__icon' iconName = 'copy' />
                                    </Button>
                                    <Button className = 'list__items-item-btn icon-btn on-dark' data-book-index = { books.indexOf(book) }
                                            title = 'Убрать из моей литературы' onClick = { removeFromMyLiterature }>
                                        <Icon className = 'list__items-item-btn-icon btn__icon' iconName = 'cross' />
                                    </Button>
                                </ButtonsContainer>
                            </ListItem>
                        )) }
                    </ListItems>
                </List>
            ) : <MyLiteratureEmpty /> }
        </div>
    )
};

export default MyLiterature;