export const tabsInitialState = {
    tabs: {
        'literature': { id: 0, text: 'Каталог', active: true },
        'my-literature': { id: 1, text: 'Моя литература', active: false }
    }
};
  
export const tabsActions = {
    selectTab: (state, { name }) => {
        const tabs = Object.assign({}, state.tabs);

        for (let key of Object.keys(tabs)) {
            tabs[key].active = false;
        }

        return {
            tabs: {
                ...tabs,
                [name]: { ...state.tabs[name], active: true }
            }
        }
    }
};