import React from 'react';
import { Link, withRouter } from 'react-router-dom';

import './SectionItem.scss';

const SectionItem = withRouter(({ text, icon, link, location }) => (
    <Link to = { link }>
        <div className = { link === location.pathname ? 'section-item section-item_active' : 'section-item'}>
            <span className = 'section-item__icon icon'>{ icon }</span>
            <span className = 'section-item__text text'>{ text }</span>
        </div>
    </Link>
));

export { SectionItem };