import React from 'react';

import booksOnTheGround from 'static/img/books-on-the-ground.png';

const MyLiteratureEmpty = () => (
    <div className = 'my-literature__empty'>
        <div className = 'my-literature__empty-title'>Моя литература</div>
        <div className = 'my-literature__empty-text'>
            Выбери книги из поиска слева, чтобы скачать их описание списком
        </div>
        <img className = 'my-literature__empty-img' src = { booksOnTheGround } alt = 'Книги на земле' />
    </div>
);

export default MyLiteratureEmpty;