import React, { useEffect, useCallback } from 'react';
import { Switch, Route } from 'react-router-dom';

import LiteratureEmpty from './LiteratureEmpty';

import { Text, InputContainer, InputsGroup, Input, InputText, 
         InputField, Form, ButtonsContainer, Button, 
         SectionItem, Icon, List, ListTitle, ListItems, ListItem } from 'components/dumb-components';

import { useStore } from 'store/useStore';

import './Literature.scss';

const Literature = ({ debounceEvent, openBookLink, clearBooks }) => {
    const { state, dispatch } = useStore();

    const searchInputs = state.inputs.search;
    const books = state.books.literature.items;
    const booksSelectedItemId = state.books.literature.selectedItemId;

    const handleInputChangeEnd = debounceEvent(event => {
        dispatch({ type: 'getBooks', payload: { dispatch: dispatch, addNew: false } });
    }, 380);

    const handleInputChange = useCallback(event => {
        dispatch({ 
            type: 'setInputValue', 
            payload: { 
                name: event.target.name, 
                value: event.target.value,
                group: 'search' 
            }
        });

        handleInputChangeEnd(event);
    }, [dispatch]);

    const handleInputBlur = useCallback(event => {
        dispatch({ type: 'blurInput', payload: { name: event.target.name, group: 'search' }});
    }, [dispatch]);

    const handleFormSubmit = useCallback(event => {
        event.preventDefault();
        dispatch({ type: 'getBooks', payload: { dispatch: dispatch, addNew: false } });
    }, [dispatch]);

    const handleListScroll = useCallback(event => {
        if (event.target.scrollTop + event.target.clientHeight >= event.target.scrollHeight) {
            dispatch({ type: 'getBooks', payload: { dispatch: dispatch, addNew: true }});
        }
    }, [dispatch]);

    const handleListKeyDown = useCallback(event => {
        if (event.key === 'ArrowDown' || event.key === 'ArrowUp' || event.key === 'ArrowRight') {
            dispatch({ type: 'selectListItem', payload: { dispatch: dispatch, name: 'literature', keyCode: event.key } })
        }
    }, [dispatch]);

    const addToMyLiterature = useCallback(event => {
        dispatch({ type: 'addToMyLiterature', payload: { book: event.currentTarget.dataset.book } });
    }, [dispatch]);

    useEffect(() => {
        window.addEventListener('keydown', handleListKeyDown);

        return () => {
            window.removeEventListener('keydown', handleListKeyDown);
        }
    }, []);

    useEffect(() => {
        if (document.querySelector('.item_active')) {
            document.querySelector('.item_active').scrollIntoView(false);
        }
    }, [booksSelectedItemId]);

    return (
        <div className = 'literature'>
            <div className = 'literature__sections'>
                <SectionItem text = 'Поиск' icon = { <Icon iconName = 'search' /> } link = '/' />
                <SectionItem text = 'Полезные ссылки' icon = { <Icon iconName = 'link' /> } link = '/helpful-links' />
            </div>

            <Switch>
                <Route exact path = '/' render = { () => (
                    <div className = 'literature__container'>
                        <Text className = 'literature__container-title'>
                            Чтобы найти получить библиографическое описание, найдите источник литературы по названию, автору или ISBN
                        </Text>

                        <Form className = 'search-form' onSubmit = { handleFormSubmit }>
                            <InputsGroup>
                                <InputContainer size = 'lg' error = { searchInputs.name.error ? 'error' : '' } 
                                                active = { searchInputs.name.active && searchInputs.name.selectItems.length !== 0 ? 'active' : '' }>
                                    <InputField>
                                        <Input name = 'name' value = { searchInputs.name.value } tabIndex = { 1 }
                                               placeholder = 'Прозведение, автор, название, ISBN' onChange = { handleInputChange } onBlur = { handleInputBlur } />
                                        <Button className = 'input__btn btn_dark'>
                                            <Text className = 'input__btn-text'>Найти</Text>
                                        </Button>
                                    </InputField>
                                    <InputText>{ searchInputs.name.text }</InputText>
                                </InputContainer>

                                <InputContainer error = { searchInputs.publishedDate.error ? 'error' : '' }
                                                active = { searchInputs.publishedDate.active && searchInputs.publishedDate.selectItems.length !== 0 ? 'active' : '' }>
                                    <InputField>
                                        <Input name = 'publishedDate' value = { searchInputs.publishedDate.value } tabIndex = { 2 }
                                               placeholder = 'Год издания' onChange = { handleInputChange } onBlur = { handleInputBlur } />
                                    </InputField>
                                    <InputText>{ searchInputs.publishedDate.text }</InputText>
                                </InputContainer>

                                <InputContainer error = { searchInputs.publisher.error ? 'error' : '' }
                                                active = { searchInputs.publisher.active && searchInputs.publisher.selectItems.length !== 0 ? 'active' : '' }>
                                    <InputField>
                                        <Input name = 'publisher' value = { searchInputs.publisher.value } tabIndex = { 3 }
                                               placeholder = 'Издательство' onChange = { handleInputChange } onBlur = { handleInputBlur } />
                                    </InputField>
                                    <InputText>{ searchInputs.publisher.text }</InputText>
                                </InputContainer>

                                <InputContainer error = { searchInputs.category.error ? 'error' : '' }
                                                active = { searchInputs.category.active && searchInputs.category.selectItems.length !== 0 ? 'active' : '' }>
                                    <InputField>
                                        <Input name = 'category' value = { searchInputs.category.value } tabIndex = { 4 }
                                               placeholder = 'Категория' onChange = { handleInputChange } onBlur = { handleInputBlur } />
                                    </InputField>
                                    <InputText>{ searchInputs.category.text }</InputText>
                                </InputContainer>

                                <InputContainer error = { searchInputs.language.error ? 'error' : '' } tabIndex = { 5 }
                                                active = { searchInputs.language.active && searchInputs.language.selectItems.length !== 0 ? 'active' : '' }>
                                    <InputField>
                                        <Input name = 'language' value = { searchInputs.language.value } tabIndex = { 6 }
                                               placeholder = 'Язык' onChange = { handleInputChange } onBlur = { handleInputBlur } />
                                    </InputField>
                                    <InputText>{ searchInputs.language.text }</InputText>
                                </InputContainer>
                            </InputsGroup>
                        </Form>
                        
                        { books.length !== 0 ? (
                            <List className = 'on-light'>
                                <ListTitle>
                                    <Text className = 'list__title-text text'>{ `${books.length} результатов` }</Text>
                                    <ButtonsContainer className = 'list__title-buttons-container'>
                                        <Button className = 'list__title-btn advanced-btn on-light' data-books-name = 'literature' onClick = { clearBooks }>
                                            <Text className = 'list__title-btn-text advanced-btn__text btn__text'>очистить</Text>
                                            <Icon className = 'list__title-btn-icon advanced-btn__icon btn__icon' iconName = 'cross-tiny' />
                                        </Button>
                                    </ButtonsContainer>
                                </ListTitle>

                                <ListItems onScroll = { handleListScroll }>
                                    { books.map(book => (
                                        <ListItem key = { book.id } active = { book.active }>
                                            <Text className = 'list__items-item-text text'>{ `${book.title}` }</Text>
                                            <ButtonsContainer className = 'list__items-item-buttons-container'>
                                                <Button className = 'list__items-item-btn icon-btn on-light' data-book-link = { book.link }
                                                        title = 'Перейти на сайт с книгой' onClick = { openBookLink }>
                                                    <Icon className = 'list__items-item-btn-icon btn__icon' iconName = 'link' />
                                                </Button>
                                                <Button className = 'list__items-item-btn icon-btn on-light' data-book = { JSON.stringify(book) }
                                                        title = 'Добавить в мою литературу' onClick = { addToMyLiterature }>
                                                    <Icon className = 'list__items-item-btn-icon btn__icon' iconName = 'plus' />
                                                </Button>
                                            </ButtonsContainer>
                                        </ListItem>
                                    )) }
                                </ListItems>
                            </List>  
                        ) : <LiteratureEmpty text = 'В нашей базе содержится более 2 000 000 источников информации' /> }                
                    </div>
                 ) } />
            </Switch>
        </div>
    )
};

export default Literature;