export const inputsInitialState = {
    inputs: {
        search: {
            name: { value: '', text: '', valid: true },
            publishedDate: { value: '', text: '', valid: true },
            publisher: { value: '', text: '', valid: true },
            category: { value: '', text: '', valid: true },
            language: { value: '', text: '', valid: true },
            valid: true
        }
    }
};

export const inputsActions = {
    setInputValue: (state, { name, value, group }) => {
        const error = false;
        const text = '';

        return {
            inputs: {
                ...state.inputs,
                [group]: {
                    ...state.inputs[group],
                    [name]: { 
                        ...state.inputs[group][name], 
                        value: value,
                        error: error, 
                        text: text,
                    }
                }
            },

            books: {
                ...state.books,
                literature: { ...state.books.literature, page: 0 }
            }
        }
    },
    
    validateInputs: (state, { group }) => {
        const groupInputs = Object.assign({}, state.inputs[group]);

        let valid = true;
        
        for (let key of Object.keys(groupInputs)) {
            if (typeof groupInputs[key] === 'object' && groupInputs[key].value.trim() === '' && groupInputs[key].required) {
                groupInputs[key].text = 'Поле не может быть пустым';
                groupInputs[key].error = true;

                valid = false;
            } 
        }

        return {
            inputs: { [group]: { ...groupInputs, valid: valid  } }
        }
    },

    setInputSelectItems: (state, { name, group, selectItems, page }) => {
        return {
            inputs: {
                ...state.inputs,
                [group]: {
                    ...state.inputs[group],
                    [name]: { 
                        ...state.inputs[group][name],
                        selectItems: selectItems,
                        page: page, 
                        active: true 
                    }
                }
            }
        }
    },

    selectInputSelectItem: (state, { dispatch, name, keyCode, group }) => {
        const input = state.inputs[group][name];
        const selectItems = Object.assign([], input.selectItems);

        let selectedItemId = input.selectedItemId > -1 ? input.selectedItemId : -1;
        let selectedItemText = input.value;
        let inputSearchValue = input.searchValue;

        if (keyCode === 'ArrowDown') {
            if (selectItems[selectedItemId] && selectedItemId !== selectItems.length - 1) selectItems[selectedItemId].active = false;

            if (selectItems[selectedItemId + 1]) {
                selectItems[selectedItemId + 1].active = true;
                selectedItemText = selectItems[selectedItemId + 1].title;
                selectedItemId++;
            }
        }

        if (keyCode === 'ArrowUp') {
            if (selectItems[selectedItemId] && selectedItemId !== 0) selectItems[selectedItemId].active = false;

            if (selectItems[selectedItemId - 1]) {
                selectItems[selectedItemId - 1].active = true;
                selectedItemText = selectItems[selectedItemId - 1].title;
                selectedItemId--;
            }
        }

        if (keyCode === 'Tab') {
            inputSearchValue = selectedItemText;
        }

        dispatch({ type: 'setInputValue', payload: { name: name, value: selectedItemText, searchValue: inputSearchValue, group: group } })
        
        return {
            inputs: {
                ...state.inputs,
                [group]: {
                    ...state.inputs[group],
                    [name]: { 
                        ...state.inputs[group][name], 
                        selectedItemId: selectedItemId,
                        selectItems: selectItems,
                        active: true
                    }
                }
            }
        }
    },
    
    blurInput: (state, { name, group }) => {
        return {
            inputs: {
                ...state.inputs,
                [group]: {
                    ...state.inputs[group],
                    [name]: { 
                        ...state.inputs[group][name],
                        page: 0 
                    }
                }
            }
        }
    }
}