import React, { useEffect } from 'react';

import './MobileTabSwitch.scss';

const MobileTabSwitch = ({ tabs, selectTab }) => {
    let activeTabName = '';

    for (let key of Object.keys(tabs)) {
        if (tabs[key].active) activeTabName = key;
    }

    useEffect(() => {
        if (document.querySelector('.mobile-tab-switch__tab_active')) {
            const tab = document.querySelector('.mobile-tab-switch__tab_active');
            const indicator = document.querySelector('.mobile-tab-switch__indicator');

            if (activeTabName === 'literature' && document.documentElement.clientWidth < 767) {
                document.querySelector('.literature').style.transform = 'translateX(0)';
                document.querySelector('.my-literature').style.transform = 'translateX(100%)';
            } 

            if (activeTabName === 'my-literature' && document.documentElement.clientWidth < 767) {
                document.querySelector('.literature').style.transform = 'translateX(-100%)';
                document.querySelector('.my-literature').style.transform = 'translateX(-100%)';
            }

            indicator.style.width = `${tab.getBoundingClientRect().width}px`;
            indicator.style.left = `${tab.offsetLeft}px`;
        }
    });

    return (
        <div className = 'mobile-tab-switch'>
            { Object.keys(tabs).map(tab => (
                <div key = { tabs[tab].id } className = {`mobile-tab-switch__tab mobile-tab-switch__tab_${tabs[tab].active ? 'active' : ''}`} 
                     data-tab-name = { tab } onClick = { selectTab }>
                    <span className = 'mobile-tab-switch__tab-text'>{ tabs[tab].text }</span>
                </div>
            )) }
            <div className = 'mobile-tab-switch__indicator'></div>
        </div>
    )
}
export default MobileTabSwitch;