import React, { useCallback } from 'react';

import Literature from 'components/smart-components/Literature/Literature';
import MyLiterature from 'components/smart-components/MyLiterature/MyLiterature';

import MobileTabSwitch from 'components/dumb-components/MobileTabSwitch/MobileTabSwitch';

import { debounce } from 'lodash';
import { useStore } from 'store/useStore';

import './Main.scss'

const Main = () => {
    const { state, dispatch } = useStore(); 
    const tabs = state.tabs;

    const selectTab = event => {
        dispatch({ type: 'selectTab', payload: { name: event.currentTarget.dataset.tabName } });
    };

    const debounceEvent = (...args) => {
        const debouncedEvent = debounce(...args);

        return event => {
            event.persist();

            return debouncedEvent(event);
        };
	};

	const writeToClipBoard = event => {
		const text = event.currentTarget.dataset.clipboardText;
		navigator.clipboard.writeText(text);
	};

	const openBookLink = event => {
        window.open(event.currentTarget.dataset.bookLink,'_blank');
	};
	
	const clearBooks = useCallback(event => {
        dispatch({ type: 'clearBooks', payload: { name: event.currentTarget.dataset.booksName } });
    }, [dispatch]);

    return (
        <main className = 'main'>
            <Literature debounceEvent = { debounceEvent } openBookLink = { openBookLink } clearBooks = { clearBooks } />
			<MyLiterature writeToClipBoard = { writeToClipBoard } openBookLink = { openBookLink } clearBooks = { clearBooks } />
            <MobileTabSwitch tabs = { tabs } selectTab = { selectTab } />
        </main>
    )
};

export default Main;