import React from 'react';
import { Link } from 'react-router-dom';

import { Icon } from 'components/dumb-components/Icon/Icon';

import './Header.scss';

const Header = () => (
	<header className = 'header'>
		<div className = 'header__info'>
			<span className = 'header__info-desc'>
				Библиография — это сервис составления библиографического описания различных источников информации.
			</span>	
			<Link to = '/'>
				<Icon iconName = 'logo' className = 'header__info-logo' />
			</Link>
		</div>

		<div className = 'header__creators'>
			<a className = 'header__creators-link' href = 'https://goldcarrot.ru/' target = '_blank' rel = 'noopener noreferrer'>
				<span className = 'header__creators-text text'>Сделано в</span>
				<Icon iconName = 'gold-carrot-logo' className = 'header__creators-logo logo' />
			</a>
		</div>
	</header>
);

export default Header;