import React from 'react';

import books from 'static/img/books.png';

const LiteratureEmpty = ({ text }) => (
    <div className = 'literature__empty'>
        <img className = 'literature__empty-img' src = { books } alt = 'Книги' />
        <span className = 'literature__empty-text'>
            { text }
        </span>
    </div>
);

export default LiteratureEmpty;