import React from 'react';

import { Text, ButtonsContainer, Button, Icon } from 'components/dumb-components';

const MyLiteratureBuild = ({ buildLink, writeToClipBoard }) => (
    <div className = 'my-literature__build'>
        <Text className = 'my-literature__build-title'>
            Сформировав свой список литературы, вы можете скачать его или распечатать. Также вам всегда будет доступна эта сборка по ссылке.
        </Text>

        <ButtonsContainer className = 'my-literature__build-buttons-container'>
            <Button className = 'advanced-btn link-btn' data-clipboard-text = { buildLink } title = 'Скопировать ссылку в буфер обмена' onClick = { writeToClipBoard }>
                <Text className = 'btn__text'>{ buildLink }</Text>
                <Icon className = 'btn__icon' iconName = 'copy' />
            </Button>
            <Button className = 'option-btn'>
                <Text className = 'btn__text'>Распечатать</Text>
            </Button>
            <Button className = 'option-btn'>
                <Text className = 'btn__text'>Скачать pdf</Text>
            </Button>
        </ButtonsContainer>
    </div>
);

export default MyLiteratureBuild;